import {Injectable, EventEmitter} from "@angular/core";
export interface Filter{
    type: string;
    dimension: string;
    value: string;
}

export interface FilterInfo{
    name: string;
}

@Injectable()
export class GlobalService {
    public domain: string = 'http://localhost:7777';
    public addFilter: EventEmitter<any>;
    public granuality: any;
    public filter:any;
    public filterInfo: FilterInfo;
    public currentInterval: any;
    public monthNames = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun',
        'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    public filterDataChange: EventEmitter<any>;


    removeFilter(dimension){
        let index = -1;
        this.filter.fields.forEach((item, i) => {
            if(item.dimension == dimension){
                index = i;
            }
        })
        if(index>-1) this.filter.fields.splice(index, 1);
    }
    
    // removeFilter(code){
    //     let index = -1;
    //     this.filter.forEach((item, i) => {
    //         if(item.dimension == dimension){
    //             index = i;
    //         }
    //     })
    //     if(index>-1) this.filter.fields.splice(index, 1);
    // }

    constructor() {
        console.log('global service enable');
        this.addFilter = new EventEmitter();
        this.filterDataChange = new EventEmitter();
        this.granuality = {
            "type": "period",
            "period": "P1M",
            "timeZone": "Etc/UTC"
        }
        this.currentInterval = {
            from: '2015-01-01T00Z',
            to: '2016-01-01T00Z'
        }
        this.filter = {
            type: "and",
            fields: []
        };
        this.filterInfo = {
            name: "mCare Dashboard"
        }
    }
}


